<template>
    <div class="modal-layout">
        <div class="darken" @click.self="closeModal(false)">
            <div class="modal-window" :class="(step>1 ? 'animated' : '') + (type=='clientDetails' ? ' thin' : '') + ( ((type=='wallet' && step==1 && actionType!='transfer') || type=='transferControl') ? ' thick' : '') + (type=='confirmAction' ? (actionType!='putCloneForceLoan' ? ' confirmation' : ' thin') : '')" v-if="showWindow">
                <div class="modal-back-button-container">
                    <button class="modal-back-button" @click="closeModal(false)"></button>
                </div>
                
                <div class="modal-container">
                    <!-- <ReplenishSigma v-if="type=='sigma' && actionType=='replenish'" :balance="balance" @closeModal="closeModal"/> -->
                    <ReplenishWallet v-if="type=='wallet' && actionType=='replenish' && step==2" :walletType="walletType" @updateModal="updateModal"  @closeModal="closeModal"/>
                    <PaymentSystemChoiceWallet v-if="type=='wallet' && step==1 && actionType!='transfer'" :actionType="actionType" @updateModal="updateModal"
                    @closeModal="closeModal"/>
                    <AttentionWallet v-if="type=='wallet' && actionType=='replenish' && step==3"
                    :walletType="walletType" :walletNumber="walletNumber" :amount="amount"  @closeModal="closeModal"/>
                    <!-- <WithdrawalSigma v-if="type=='sigma' && actionType=='withdrawal'"  @closeModal="closeModal"/> -->
                    <WithdrawalWallet v-if="type=='wallet' && actionType=='withdrawal' && step==2" :walletType="walletType"  @closeModal="closeModal"/>
                    <InternalTransfer v-if="type=='wallet' && actionType=='transfer'" @closeModal="closeModal"/>
                    <StructureClientProfile v-if="type=='clientDetails'" :clientId="clientId" @closeModal="closeModal" />
                    <ConfirmAction v-if="type=='confirmAction'" :actionType="actionType" :loanAvailableAmount="loanAvailableAmount" @closeModal="closeModal" />

                    <TransferControlChoice v-if="type=='transferControl' && step==1" :sponsorNickname="sponsorNickname" :sponsorSponsorNickname="sponsorSponsorNickname" @closeModal="closeModal" @updateModal="updateModal"/>
                    <TransferControl v-if="type=='transferControl' && step==2" :transferControlNickname="transferControlNickname" @closeModal="closeModal" @updateModal="updateModal"/>
                    <VideoReview v-if="type=='videoReview'" @closeModal="closeModal" />
                    <ViewReward v-if="type=='viewReward'" @closeModal="closeModal" />
                </div>
            </div>
        </div>
      
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import ReplenishSigma from "@/components/modals/ReplenishSigma.vue"
import ReplenishWallet from "@/components/modals/ReplenishWallet.vue"
import AttentionWallet from "@/components/modals/AttentionWallet.vue"
import WithdrawalSigma from "@/components/modals/WithdrawalSigma.vue"
import WithdrawalWallet from "@/components/modals/WithdrawalWallet.vue"
import PaymentSystemChoiceWallet from "@/components/modals/PaymentSystemChoiceWallet.vue"
import InternalTransfer from "@/components/modals/InternalTransfer.vue"
import StructureClientProfile from "@/components/modals/StructureClientProfile.vue"
import ConfirmAction from "@/components/modals/ConfirmAction.vue"
import TransferControlChoice from "@/components/modals/TransferControlChoice.vue"
import TransferControl from "@/components/modals/TransferControl.vue"
import VideoReview from "@/components/modals/VideoReview.vue"
import ViewReward from "@/components/modals/ViewReward.vue"

export default defineComponent({
    setup() {
        
    },
    data: () => ({
        walletType: '',
        walletNumber: '',
        amount: 0,
        showWindow: true,
        transferControlNickname: '',
    }),
    props: {
        balance: {
            type: Number,
            default: 0
        },
        type: { // sigma, wallet
            type: String, 
            required: true
        },
        actionType: { // replenish, withdrawal
            type: String, 
            required: true
        },
        step: { // 1,2
            type: Number, 
            default: 1
        },
        sponsorNickname: String,
        sponsorSponsorNickname: String,
        loanAvailableAmount: Number,
        clientId: Number
    },
    components: {
        ReplenishSigma, ReplenishWallet, AttentionWallet, WithdrawalSigma, WithdrawalWallet, PaymentSystemChoiceWallet, InternalTransfer, StructureClientProfile, ConfirmAction, TransferControlChoice, TransferControl, VideoReview, ViewReward 
    },
    methods: {
         closeModal(confirmed) {
            this.$emit('closeModal', confirmed)
        },
        updateModal(step, ...data) {
            if(this.type=='transferControl'){
                let [transferControlNickname = ''] = data;
                this.showWindow = false
                this.transferControlNickname = transferControlNickname;
                this.$emit('updateModal', step, transferControlNickname)
                setTimeout(()=>{
                    this.showWindow = true
                },0)
            } else {
                let [walletType = '', walletNumber='', amount=0] = data;
                this.showWindow = false
                this.walletType = walletType
                this.walletNumber = walletNumber
                this.amount = amount
                this.$emit('updateModal', step)
                setTimeout(()=>{
                    this.showWindow = true
                },0)
            }
            
            
        },

    }
})
</script>

