<template>
    <div class="modal-content">
        <p class="window-title">{{ $localizeFilter('Кому вы хотите передать управление вашим аккаунтом?') }}</p>
        <!-- <p class="window-text">{{$localizeFilter('replenish_1_text')}}</p> -->

        <div class="control-transfer-option-list">
            <div class="control-transfer-option" @click="submitHandler(sponsorNickname)">
                Ваш спонсор логин <span class="orange">{{sponsorNickname}}</span>
            </div>
            <div class="control-transfer-option" @click="submitHandler(sponsorSponsorNickname)">
                Спонсор спонсора логин <span class="orange">{{sponsorSponsorNickname}}</span>
            </div>
            <div class="control-transfer-option" @click="submitHandler('')">
                Другой пользователь
            </div>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { required, numeric, helpers } from "@vuelidate/validators";
import Toggle from "@/components/Toggle.vue"
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    components: {
        Toggle
    },
    props: ['sponsorNickname', 'sponsorSponsorNickname'],
    data: () => ({
    }),
     setup() {
        return { v$: useVuelidate() };
    },
    validations() {
        return {
            amount: {
                required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
                numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric)
            }
        };
    },
    methods: {
        async submitHandler(transferControlNickname) {
            console.log(transferControlNickname)
            try {
                this.$emit('updateModal', 2, transferControlNickname)
            } catch(e) {
                this.$error(e)
            } 
        }
    }
})
</script>

