<template>
    <div class="modal-content">
        <p class="window-title">{{$localizeFilter('withdrawal')}}</p>

        <p class="window-text">{{$localizeFilter('modal_withdrawal_text_1')}}</p>
          <div class="input-field">
            <input id="amount" class="custom-input" :placeholder="'Сумма в dream'" type="text" v-model.trim="amount" :class="{ invalid: v$.amount.$error }"/>
            <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                
                {{ error.$message }}
            </small>
        </div>

        <p class="window-text">{{$localizeFilter('modal_withdrawal_text_2') + ' (' + paymentMethodMap[walletType] + ')'}}</p>
          <div class="input-field">
            <input id="address" class="custom-input wallet" :placeholder="$localizeFilter('wallet_number') + ' ' + paymentMethodMap[walletType]" type="text" v-model.trim="address" :class="{ invalid: v$.address.$error }"/>
            <small class="error" v-for="(error, index) of v$.address.$errors" :key="index">
                
                {{ error.$message }}
            </small>
        </div>

        <p class="window-text">{{$localizeFilter('modal_withdrawal_text_3')}}</p>

        <p class="window-text" v-if="confirmationCodeSent">{{$localizeFilter('modal_transfer_text_3')}}</p>
          <div class="input-field" v-if="confirmationCodeSent">
            <input id="confirmation_code" class="custom-input wallet" :placeholder="$localizeFilter('modal_transfer_enter_confirmation_code')" type="text" v-model.trim="confirmationCode"/>
        </div>

         <label class="error" v-if="notEnoughFunds">{{$localizeFilter('calculator_3_not_enough_funds')}}</label>
         <label class="error" v-if="wrongPaymentConfirmationCode">{{$localizeFilter('wrong_or_expired_payment_confirmation_code')}}</label>

        <button class="dark-button" :class="conditionsAccepted ? '' : 'inactive'" @click="submitHandler">{{$localizeFilter('next')}}</button>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { required, numeric, helpers } from "@vuelidate/validators";
import Toggle from "@/components/Toggle.vue"
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    components: {
        Toggle
    },
    props: {
        walletType: String
    },
    data: () => ({
        amount: "",
        address: "",
        selectedMethod: '',
        notEnoughFunds: false,
        wrongPaymentConfirmationCode: false,
        conditionsAccepted: true,
        paymentMethodMap: {
            'PAYEER': 'PAYEER',
            'USDT': 'USDT:TRC-20',
            'PERFECT_MONEY': 'Perfect Money',
            'USDT_BEP20': 'USDT:BEP-20'
        },
        confirmationCode: "",
        confirmationCodeSent: false,
    }),
     setup() {
        return { v$: useVuelidate() };
    },
    validations() {
        return {
            amount: {
                required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
                numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric)
            },
            address: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) }
        };
    },
    methods: {
        async submitHandler() {
            this.v$.$touch();
            if (this.v$.$error || !this.conditionsAccepted) {
                return;
            }

            if(!this.confirmationCodeSent) {
                await this.$store.dispatch('generatePaymentConfirmationCode', {});
                this.confirmationCodeSent = true;
                return;
            }

            try {
                let method = JSON.parse(JSON.stringify(this.selectedMethod))
                if (method=='USDT') method='USDT.TRC20'
                else if (method=='USDT_BEP20') method='USDT.BEP20'
                const formData = {amount: this.amount, withdrawWalletAddress: this.address, withdrawMethod: method, confirmationCode: this.confirmationCode}
                await this.$store.dispatch('createWithdrawBalanceRequest', formData)
                this.$message(localizeFilter('withdraw_successfull'))
                this.$emit('closeModal', false)
                // this.$emit('updateModal', 2, this.selectedMethod, responseData.address, responseData.transferAmount)
            } catch(e) {
                if (e.message == 'Not enough funds') {
                    this.notEnoughFunds = true
                    this.wrongPaymentConfirmationCode = false
                } else if(e.message == 'Wrong payment confirmation code') {
                    this.notEnoughFunds = false
                    this.wrongPaymentConfirmationCode = true
                } else {
                    this.notEnoughFunds = false
                    this.wrongPaymentConfirmationCode = false
                    this.$error(e)
                }
            } 
        },
        acceptСonditions() {
            this.conditionsAccepted = !this.conditionsAccepted
        }   
    },
     async mounted() {
        try {
            const data = await this.$store.dispatch('getWithdrawCredentials')
            let cachedWithdrawMethod = data.withdrawMethod == 'USDT:TRC20' ? 'USDT' : data.withdrawMethod;
            this.address = cachedWithdrawMethod == this.walletType ? data.withdrawWalletAddress : ''
            this.selectedMethod = this.walletType
            // console.log(this.selectedMethod)
            this.$forceUpdate()
        } catch(e) {
            this.$error(e)
        } 
    }
})
</script>

