    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}
.dark-button {
    padding: 10px 16px 10px 16px;
    margin-top: 16px;
}
.window-text {
    text-align: left;
}

i {
    margin: 0 6px 0 0;
}

.window-text {
    margin: 16px 0 0 0;
}

.modal-warning {
    margin: 0 0 0 -2;
    padding: 0 0 0 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.window-text.warning {
    margin: 0 0 0 0;
}

.window-title {
    margin: 0 0 0 0;
}
.input-field {
    margin-top: 8px!important;
    margin-bottom: 0!important;
}

.error {
    margin-top: 16px;
    font-size: 14px;
    line-height: 16px;
    color: #FF0000;
}
</style>
