<template>
    <div class="modal-content">
        <p class="window-title">{{$localizeFilter('replenish_1_title')}}</p>
        <p class="window-text">{{$localizeFilter('replenish_1_text')}}</p>

          <div class="input-field">
            <input id="amount" class="custom-input" :placeholder="$localizeFilter('enter_amount')" type="text" v-model.trim="amount" :class="{ invalid: v$.amount.$error }"/>
            <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                
                {{ error.$message }}
            </small>
        </div>

         <div class="modal-warning">
            <i class="material-icons right" style="color: orange!important;">report</i>
            <p class="window-text warning">{{$localizeFilter('replenish_sigma_warning_1')}}</p>
        </div>

        <button class="dark-button" :class="conditionsAccepted ? '' : 'inactive'" @click="submitHandler">{{$localizeFilter('next')}}</button>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { required, numeric, helpers } from "@vuelidate/validators";
import Toggle from "@/components/Toggle.vue"
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    components: {
        Toggle
    },
    props: {
        walletType: String
    },
    data: () => ({
        amount: "",
        // selectedMethod: 'BTC',
        conditionsAccepted: true //false
    }),
     setup() {
        return { v$: useVuelidate() };
    },
    validations() {
        return {
            amount: {
                required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
                numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric)
            }
        };
  },
  methods: {
      async submitHandler() {
        this.v$.$touch();
        if (this.v$.$error || !this.conditionsAccepted) {
            return;
        }
        try {
            let method = this.walletType;
            if (method=='USDT') method='USDT.TRC20'
            else if (method=='USDT_BEP20') method='USDT.BEP20'
            const formData = {amount: this.amount, method: method}
            const responseData = await this.$store.dispatch('createFillBalanceRequest', formData)
            // this.$message(localizeFilter('balance_replenished'))
            this.$emit('updateModal', 3, this.walletType, responseData.address, responseData.transferAmount)
        } catch(e) {
            this.$error(e)
        } 
      },
    acceptСonditions() {
        this.conditionsAccepted = !this.conditionsAccepted
    }
  }
})
</script>

