<template>
    <div class="modal-content">
        <p class="window-title">{{$localizeFilter('replenish_sigma')}}</p>
        
        <p class="window-text">{{$localizeFilter('replenish_sigma_text')}}</p>
          <div class="input-field">
            <input id="amount" class="custom-input" :placeholder="$localizeFilter('enter_amount')" type="text" v-model.trim="amount" :class="{ invalid: v$.amount.$error }"/>
            <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                
                {{ error.$message }}
            </small>
        </div>


        <div class="modal-warning">
            <i class="material-icons right" style="color: orange!important;">report</i>
            <p class="window-text warning">{{$localizeFilter('replenish_sigma_warning_1')}}</p>
        </div>

        <p class="window-text warning">{{$localizeFilter('replenish_sigma_warning_2_1') + ' ' + balance + ' dream' + $localizeFilter('replenish_sigma_warning_2_2')}}</p>

        <label class="error" v-if="notEnoughFunds">{{$localizeFilter('calculator_3_not_enough_funds')}}</label>
        <label class="error" v-if="sigmaAmountShouldBeNotLessThan">{{$localizeFilter('sigma_amount_should_be_not_less_than')}}</label>

        <button class="dark-button" @click="submitHandler">{{$localizeFilter('next')}}</button>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { required, numeric, helpers } from "@vuelidate/validators";
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    props: ['balance'],
    data: () => ({
        amount: "",
        notEnoughFunds: false,
        sigmaAmountShouldBeNotLessThan: false
    }),
     setup() {
        return { v$: useVuelidate() };
    },
    validations() {
        return {
            amount: {
                required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
                numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric)
            }
        };
  },
  methods: {
      async submitHandler() {
        this.v$.$touch();
        if (this.v$.$error) {
            return;
        }
        try {
            const formData = {amount: this.amount}
            await this.$store.dispatch('fillSigma', formData)
            this.$message(localizeFilter('balance_sigma_replenished'))
            this.$emit('closeModal', true)
        } catch(e) {
            if (e.message == 'Not enough funds') {
                this.notEnoughFunds = true
                this.sigmaAmountShouldBeNotLessThan = false
            // } else if(e.message == 'Sigma fill amount should be a multiple of 1000'){
            } else if(e.message == 'Sigma fill amount should be not less than 10'){
                this.sigmaAmountShouldBeNotLessThan = true
                this.notEnoughFunds = false
            } else {
                this.sigmaAmountShouldBeNotLessThan = false
                this.notEnoughFunds = false
                this.$error(e)
            }
        } 
      }
  }
})
</script>

