<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import {api} from "@/plugins/api";
import globalConfig from '@/globalConfig'

let domain = globalConfig.getHostName();

export default defineComponent({
    props: [
        'walletType', 'walletNumber', 'amount' 
    ],
    data: () => ({
        paymentMethodMap: {
            'PAYEER': 'PAYEER',
            'USDT': 'USDT',
            'PERFECT_MONEY': 'Perfect Money',
            'USDT_BEP20': 'USDT:BEP-20',
            'ADV_CASH': 'Advcash',
        },
        screenshot: null
    }),
     setup() {
    },
  methods: {
      async uploadFile(event){
        console.log(event.target.files[0]);
        
        try {
            let data = await api.uploadScreenshot(event.target.files[0])
            this.screenshot = data.data.filename
        } catch(e) {
            console.log(e);
        } finally {
            this.$refs.inputFile.value = null;
        }
        // let file = 
      },
      async submitHandler() {

            // if (!this.screenshot) {
            //     return;
            // }
            if(this.walletType == 'USDT') {
                this.$emit('close')
                return;
            }
            window.open('https://t.me/obmenMaks', '_blank');
            // try {
            //     let paymentMethodConversion = {
            //         'USDT_BEP20': 'USDT.BEP20',
            //         'USDT': 'USDT.TRC20',
            //     }
            //     const formData = {amount: this.amount, method: paymentMethodConversion[this.walletType] || this.walletType}
            //     console.log(formData)
            //     const responseData = await this.$store.dispatch('createFillBalanceRequest', formData)
            //     this.$emit('closeModal', false)
            // } catch(e){
            //     console.log(e)
            // }
          
      },
      copy(value) {
            function fallbackCopyTextToClipboard(text) {
                var textArea = document.createElement("textarea");
                textArea.value = text;
                
                // Avoid scrolling to bottom
                textArea.style.top = "0";
                textArea.style.left = "0";
                textArea.style.position = "fixed";

                document.body.appendChild(textArea);
                textArea.focus();
                textArea.select();
                var successful;
                try {
                    successful = document.execCommand('copy');
                    var msg = successful ? 'successful' : 'unsuccessful';
                    console.log('Fallback: Copying text command was ' + msg);
                } catch (err) {
                    console.error('Fallback: Oops, unable to copy', err);
                }

                document.body.removeChild(textArea);
                return successful;
            }
            if (value) {
                const clipboardData =
                // event.clipboardData ||
                window.clipboardData ||
                // event.originalEvent?.clipboardData ||
                navigator.clipboard;
                try {
                    clipboardData.writeText(value); 
                    this.$message(localizeFilter('copied')) 
                } catch (e) {
                    if(fallbackCopyTextToClipboard(value)){
                        this.$message(localizeFilter('copied')) 
                    }
                }
            }
      }
  },
  computed: {
      screenshotLink(){
          return domain + '/screenshots/' + this.screenshot
      }
  }
})
</script>

