<template>
    <div class="modal-content">
        <p class="window-title">{{ $localizeFilter('Кому вы хотите передать управление вашим аккаунтом?') }}</p>
        <!-- <p class="window-text">{{$localizeFilter('replenish_1_text')}}</p> -->

        <p class="window-text">{{$localizeFilter('Логин пользователя кому вы передаете управление')}}</p>
        <div class="input-field">
            <input id="transferControlNickname" class="custom-input" type="text" v-model.trim="nickname"/>
        </div>

        <button class="blue-button" v-if="!managerNickname" @click="submitHandler()">{{ $localizeFilter('Передать управление') }}</button>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { required, numeric, helpers } from "@vuelidate/validators";
import Toggle from "@/components/Toggle.vue"
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    components: {
        Toggle
    },
    props: ['transferControlNickname'],
    data: () => ({
        nickname: ''
    }),
     setup() {
        return { v$: useVuelidate() };
    },
    validations() {
        return {
            amount: {
                required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
                numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric)
            }
        };
  },
  mounted() {
    this.nickname = this.transferControlNickname
  },
  methods: {
      async submitHandler() {
        //   console.log(transferControlNickname)
        try {
            this.$emit('updateModal', 3, this.nickname)
        } catch(e) {
            this.$error(e)
        } 
      }
  }
})
</script>

